import React, {useState, useEffect, useCallback }  from 'react'
import { withRouter } from 'react-router-dom'
import axios from 'axios'
//import { Button } from '@mui/material';
import Button from '@material-ui/core/Button'
import { Form, Row, Col} from 'react-bootstrap';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import XLSX from 'xlsx'
import { saveAs } from 'file-saver'
import { DataGrid, GridToolbar} from '@mui/x-data-grid';
import Dialog from '@mui/material/Dialog';
import Box from '@mui/material/Box';
import DialogContent from '@mui/material/DialogContent';
import CircularProgress from '@mui/material/CircularProgress'

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});



function CommenceNewSpecificTaskBody(props) {

    let [users, setUsers] = useState([]);
    let [companies, setCompanies] = useState([]);
    let [severity, setSeverity] = useState("");
    let [alart, setAlart] = useState("");
    const axiosInstance = axios.create({
        baseURL: process.env.REACT_APP_BACKEND_URL,
    })
    
    let [form, setForm] = useState({})
    let [errors, setErrors] = useState({})
    let [open, setOpen] = useState(false);
    let [openDialog, setOpenDialog] = useState(false);

    let setField = (field, value) => {
    setForm({
            ...form,
            [field]: value
        })
        if ( !!errors[field] ) setErrors({
            ...errors,
            [field]: null
        })
    }
    
    const findFormErrors = () => {
        const { taskassignedSubconOfficer, taskassignedMobileNo, taskCommencedComment} = form
        const newErrors = {}
        // name errors
        if ( !taskassignedSubconOfficer || taskassignedSubconOfficer === '' ) newErrors.taskassignedSubconOfficer = 'cannot be blank!'
        if ( !taskassignedMobileNo || taskassignedMobileNo === '' ) newErrors.taskassignedMobileNo = 'cannot be blank!'
        if ( !taskCommencedComment || taskCommencedComment === '' ) newErrors.taskCommencedComment = 'cannot be blank!'


        return newErrors
    }

    
    useEffect(() => {
        // console.log(props.location.data.row.assignedSubcon)
        setUsers([{name:props.location.data.row.assignedMobitelOfficer.name}])
        setCompanies([{companyName:props.location.data.row.assignedSubcon.companyName}])
        setForm({
                'taskRef':props.location.data.row.taskRef,
                'siteID':props.location.data.row.siteID,
                'siteName':props.location.data.row.siteName,
                'plannedTaskStartDate':props.location.data.row.plannedTaskStartDate.split('T')[0],
                'targetTaskComDate':props.location.data.row.targetTaskComDate.split('T')[0],
                'taskAssignedDiv':props.location.data.row.taskAssignedDiv,
                'assignedMobitelOfficer': props.location.data.row.assignedMobitelOfficer.name,
                'workScope':props.location.data.row.workScope,
                'assignedSubcon': props.location.data.row.assignedSubcon.companyName,
                'taskassignedSubconOfficer': props.location.data.row.taskassignedSubconOfficer,
                'taskassignedMobileNo': props.location.data.row.taskassignedMobileNo
            })

    } , [ ] )

    const downloadHandover = (e)=>{
        //Create Downloadable excel file
        var workbook = XLSX.utils.book_new();
        var aoa_projects = []

        //get selected projects
        //console.log(props.location.data)

        var worksheetData = []

        worksheetData.push(props.location.data.row.properties)
        worksheetData.unshift(props.location.data.row.headerProperties)
        //console.log(worksheetData)
        var ws = XLSX.utils.aoa_to_sheet(worksheetData);
        XLSX.utils.book_append_sheet(workbook, ws, "HO Details");
        
        XLSX.writeFile(workbook, props.location.data.row.siteID+"_Handover details.xlsx")
        // console.log(workbook)

    }

        //File download table
        const columns = [
            { field: 'id', headerName: 'File Name', width: 350 },
            { field: "Download File", width:200,
                renderCell: (cellValues) => {
                    return (
                    <Button
                        variant="contained"
                        color="primary"
                        onClick={(event) => {
                            downloadFiles(event, cellValues);
                        }}
                    >
                        Click to Download
                    </Button>
                    );
                }
            }
    
            ];
    
        async function downloadFiles(event, cellValues) {
            // console.log(cellValues.row.id)
            const { data } = await getFiles(cellValues.row.id)
            const blob = new Blob([data])
            saveAs(blob, cellValues.row.id)
          }
          
        const getFiles=(e)=> {
        // console.log(e)
        return axiosInstance.get('projonline/file/'+e, {
            headers: {
            token:"Bearer "+ JSON.parse(localStorage.getItem("user")).accessToken,
            'Content-Type': 'multipart/form-data'
            },
            responseType: 'arraybuffer',
            withCredentials: true
        })
        }

        const smsFetch = async (mobileNo) => {
            //console.log(users.filter((obj) => (obj._id === form.assignedMobitelOfficer))[0].mobileNo)
            const url = "https://msmsenterpriseapi.mobitel.lk/EnterpriseSMSV3/esmsproxyURL.php"
            const Body ={
                "username" : "smsdigisvc_sci", 
                "password" : "Sci@#25", 
                "from": "Mobitel-PMS", 
                "to": "0"+mobileNo, 
                "text": "Task ID:"+form.taskRef+" task is commenced now and assigned to you.",
                "mesageType":0
            }
            //console.log(JSON.parse(localStorage.getItem("user")))
            const requestOptions = {
                method: 'POST',
                body: JSON.stringify(Body),
                mode: 'no-cors'
            };
            try{
                const response = await fetch(url,requestOptions);
                // console.log(response)
            }catch(err){
                console.log(err)
            }
            
    
        };

    const onAccept=(e)=> {
        e.preventDefault()

        const newErrors = findFormErrors()
        // Conditional logic:
        if ( Object.keys(newErrors).length > 0 ) {
            // We got errors!
            setErrors(newErrors)
        } else {
            const taskObject = {
                taskassignedSubconOfficer: form.taskassignedSubconOfficer,
                taskassignedMobileNo: form.taskassignedMobileNo,
                taskStatus: 'Task commenced'
            };

            // console.log(form.assignedMobitelOfficer)

            axiosInstance.put('projonline/update/'+props.location.data.row._id, {taskObject,taskHistory:{taskStatus: 'Task commenced', comment: form.taskCommencedComment}},{headers:{token:"Bearer "+ JSON.parse(localStorage.getItem("user")).accessToken}})
                .then((res) => {
                    // console.log(res.data)

                    setOpenDialog(true)
                    smsFetch(form.taskassignedMobileNo)
                    setTimeout(() => {
                        setAlart(res.data.alart)
                        setSeverity(res.data.severity)
                        setOpen(true)
                    }, 2000)
                    
                    setTimeout(() => {
                        setOpenDialog(false)
                        setOpen(false)
                        props.history.push({pathname:'/commenceNewTasks'})
                    }, 5000)

                }).catch((error) => {
                    console.log(error)
                });
            }

    }

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
    
        setOpen(false);
    };

    
    

    return (
        <div>
            
                        {/* Content Wrapper. Contains page content */}
            <div className="content-wrapper">
            {/* Content Header (Page header) */}
            <div className="content-header">
            <section className="content-header">
                    <div className="container-fluid">
                    <div className="row mb-2">
                        <div className="col-sm-6">
                        <h1>Commence Task ID: {form.taskRef} </h1>
                        </div>
                    </div>
                    </div>{/* /.container-fluid */}
                </section>
            </div>
            {/* /.content-header */}
            {/* Main content */}
            <section className="content">
                <div className="container-fluid">
                <Form >
                    
                    <Row>
                        <Form.Group as={Col} >
                            <Form.Label>Site ID</Form.Label>
                            <Form.Control disabled value = {form.siteID} className="col-md-9" type="text" placeholder="Enter site ID" isInvalid={ !!errors.siteID }/>
                            <Form.Control.Feedback type='invalid'>
                                { errors.siteID }
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} >
                            <Form.Label>Site Name</Form.Label>
                            <Form.Control  disabled value = {form.siteName} type="text" placeholder="Enter site Name" isInvalid={ !!errors.siteName }/>
                            <Form.Control.Feedback type='invalid'>
                                { errors.siteName }
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} controlId="">
                        </Form.Group>

                    </Row>
                    

                    <Row className="mb-3">
                        <Form.Group as={Col} >
                            <Form.Label>Planned Work Commencement Date</Form.Label>
                            <Form.Control disabled value = {form.plannedTaskStartDate}  className="col-md-9" type="date" isInvalid={ !!errors.plannedTaskStartDate }/>
                            <Form.Control.Feedback type='invalid'>
                                { errors.plannedTaskStartDate }
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} >
                        <Form.Label>Planned Work Completion Date</Form.Label>
                        <Form.Control disabled value = {form.targetTaskComDate} className="col-md-9" type="date" isInvalid={ !!errors.targetTaskComDate }/>
                        <Form.Control.Feedback type='invalid'>
                                { errors.targetTaskComDate }
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} >
                        
                        </Form.Group>
                    </Row>
                    <Row>
                        <Form.Group as={Col} >
                            <Form.Label>Task Assigned Division</Form.Label>
                            <Form.Control disabled value = {form.taskAssignedDiv} as="select" defaultValue="Choose..." isInvalid={ !!errors.taskAssignedDiv }>
                                <option>Choose...</option>
                                <option>Project_Radio</option>
                                <option>Project_Core</option>
                                <option>Project_TX</option>
                                <option>Project_IBS</option>
                                <option>Project_WiFi</option>
                            </Form.Control>
                            <Form.Control.Feedback type='invalid'>
                                { errors.taskAssignedDiv }
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} >
                            <Form.Label>Assigned Mobitel Officer</Form.Label>
                            <Form.Control disabled as="select" value = {form.assignedMobitelOfficer} defaultValue="Choose..." isInvalid={ !!errors.assignedMobitelOfficer }>
                                <option>Choose...</option>
                                {users.map(opt => (
                                    <option value={opt._id}>{opt.name}</option>
                                ))}
                            </Form.Control>
                            <Form.Control.Feedback type='invalid'>
                                { errors.assignedMobitelOfficer }
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} >

                        </Form.Group>

                    </Row>
                    
                    <Row>
                        <Form.Group as={Col} >
                            <Form.Label>High level work scope</Form.Label>
                            <Form.Control disabled value={form.workScope}  as="textarea" rows={2}  placeholder="" isInvalid={ !!errors.workScope } />
                            <Form.Control.Feedback type='invalid'>
                                { errors.workScope }
                            </Form.Control.Feedback>
                        </Form.Group>

                    </Row>
                    


                    <Row className="mb-3">

                    <Form.Group as={Col} >
                        <Form.Label>Assigned subcontractor</Form.Label>
                        <Form.Control disabled value={form.assignedSubcon} as="select" defaultValue="Choose..." isInvalid={ !!errors.assignedSubcon } >
                            <option>Choose...</option>
                            {companies.map(opt => (
                                    <option value={opt._id}>{opt.companyName}</option>
                            ))}
                        </Form.Control>
                        <Form.Control.Feedback type='invalid'>
                                { errors.assignedSubcon }
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} >
                        <Form.Label>Handover Details</Form.Label>
                            <br />
                                <Button onClick={downloadHandover} color="primary" variant="contained">
                                    Click to Download
                                </Button>
                        
                        </Form.Group>

                        <Form.Group as={Col} >
                        </Form.Group>
                    </Row>

                    <Row>
                        <Form.Group as={Col} >
                        <Form.Label>Download other supporting docs</Form.Label>
                        <div style={{ height: 200, width: '50%' }}>
                            <DataGrid
                                //getRowId={(file)=>file.fileName}
                                rows={props.location.data.row.files.map(ele=>{return{"id":ele}})}
                                columns={columns}
                                pageSize={3}
                                rowsPerPageOptions={[3]}
                                rowHeight={30}
                                headerHeight= {40}
                            />
                            
                        </div>
                        </Form.Group>

                    </Row>

                    <Row>
                        <Form.Group as={Col} >
                            <Form.Label>Task Assigned Subcon Officer</Form.Label>
                            <Form.Control onChange={ e => setField('taskassignedSubconOfficer', e.target.value) } value = {form.taskassignedSubconOfficer} className="col-md-9" type="text" placeholder="Enter name" isInvalid={ !!errors.taskassignedSubconOfficer }/>
                            <Form.Control.Feedback type='invalid'>
                                { errors.taskassignedSubconOfficer }
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} >
                            <Form.Label>Mobile Number</Form.Label>
                            <Form.Control  onChange={ e => setField('taskassignedMobileNo', e.target.value) } className="col-md-9" value = {form.taskassignedMobileNo} type="tel" maxLength = "9" placeholder="Enter mobile number" isInvalid={ !!errors.taskassignedMobileNo }/>
                            <Form.Control.Feedback type='invalid'>
                                { errors.taskassignedMobileNo }
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} >
                        </Form.Group>

                    </Row>
                    <Row>

                        <Form.Group as={Col} >
                            <Form.Label>Comments</Form.Label>
                            <Form.Control  onChange={ e => setField('taskCommencedComment', e.target.value) }  as="textarea" rows={2} value = {form.taskCommencedComment}  placeholder="Enter your comments" isInvalid={ !!errors.taskCommencedComment }/>
                            <Form.Control.Feedback type='invalid'>
                                { errors.taskCommencedComment }
                            </Form.Control.Feedback>
                        </Form.Group>

                    </Row>
                    
                    <Button color="primary" variant="contained" onClick={onAccept}>
                            Task commenced
                    </Button>
                       
                    
                </Form>
                </div>{/* /.container-fluid */}
            </section>
            {/* /.content */}
            
            
            </div>

            <Snackbar open={open} autoHideDuration={6000} onClose={handleClose} anchorOrigin={{vertical: "top", horizontal: "center"}}>
                <Alert onClose={handleClose} severity={severity} sx={{ width: '100%' }} >
                    {alart}
                </Alert>
            </Snackbar>
            <Dialog
                open={openDialog}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogContent>
                    <Box sx={{ display: 'flex' }}>
                        <CircularProgress />
                    </Box>

                    
                </DialogContent>
            </Dialog>


        </div>
    )
}

export default withRouter(CommenceNewSpecificTaskBody)